.color-block {
  display: flex;
  align-items: center;
  width: 200px;
  padding-left: 10px;
  &__title {
    width: 70px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__color {
    border: 1px solid black;
    height: 10px;
    width: 10px;
    margin-right: 5px;
  }
}
.color {
  display: flex;
  flex-direction: column;
}
.item {
  margin-left: 10px;
  margin-right: 10px;
  background-color: #f8f9fa;
  padding: 15px 30px;
  border-radius: 15px;
}
.buttons {
  button {
    margin-left: 10px;
  }
}

.changing-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  .color-block {
    @media screen and (max-width: 992px) {
      margin: auto;
    }
  }
}
